"use client";

import { useState, FormEvent } from 'react';
import Image from 'next/image';
import { Search } from 'lucide-react';
import { useLanguage } from '@/lib/i18n';

export default function Hero() {
    const { t } = useLanguage();
    const [searchQuery, setSearchQuery] = useState('');

    const handleSearch = (e: FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        const formData = new FormData(e.currentTarget);
        const query = formData.get('search') as string;

        if (query.trim()) {
            // Redirect to the learning subdomain
            window.location.href = `https://learn.knowly.uz/search?q=${encodeURIComponent(query)}`;
        }
    };

    return (
        <section className="relative container mx-auto px-4 py-20 flex flex-col items-center text-center space-y-6">
            {/* Candy Gradient Background */}
            <div className="absolute inset-0 -z-10 bg-[radial-gradient(ellipse_at_top,_var(--tw-gradient-stops))] from-red-50 via-gray-50 to-white"></div>

            {/* Subtle Grid Pattern Overlay */}
            <div
                className="absolute inset-0 -z-10 opacity-10"
                style={{
                    backgroundImage: `linear-gradient(rgba(217, 45, 32, 0.1) 1px, transparent 1px), linear-gradient(90deg, rgba(217, 45, 32, 0.1) 1px, transparent 1px)`,
                    backgroundSize: '40px 40px'
                }}
            ></div>

            {/* 3D Bent Logo - Smaller */}
            <div className="w-full max-w-lg animate-bounce-slow">
                <Image
                    src="/logos/knowly-full-alt.png"
                    alt="KNOWLY"
                    width={600}
                    height={180}
                    className="w-full h-auto drop-shadow-xl"
                    style={{ maxHeight: '100px', objectFit: 'contain' }}
                    priority
                />
            </div>

            {/* Headline - Smaller */}
            <h1 className="text-3xl sm:text-4xl font-extrabold text-gray-900">
                {t.hero.headline.main}
            </h1>

            {/* Sub-headline - Smaller Red */}
            <h2 className="text-4xl md:text-5xl font-extrabold text-red-600 leading-tight">
                {t.hero.headline.highlight}
            </h2>

            {/* Subheadline */}
            <p className="text-base sm:text-lg text-gray-600 max-w-2xl">
                {t.hero.subheadline}
            </p>

            {/* Previous Search Bar Style - White box with shadow */}
            <form onSubmit={handleSearch} className="w-full max-w-2xl">
                <div className="relative bg-white rounded-3xl shadow-2xl border-2 border-gray-200 overflow-hidden">
                    <div className="flex items-center px-6 py-5">
                        <Search className="w-6 h-6 text-red-600 mr-4 flex-shrink-0" />
                        <input
                            name="search"
                            type="text"
                            value={searchQuery}
                            onChange={(e) => setSearchQuery(e.target.value)}
                            placeholder={t.hero.search.placeholder}
                            className="flex-1 text-lg font-semibold text-gray-900 bg-transparent outline-none placeholder-gray-400"
                        />
                    </div>
                </div>

                {/* Shadow effect */}
                <div className="absolute -bottom-2 left-1/2 -translate-x-1/2 w-3/4 h-4 bg-red-600 opacity-20 blur-xl rounded-full" />
            </form>

            {/* CTA Button */}
            <button className="bg-red-600 text-white px-8 py-4 rounded-2xl font-extrabold text-lg border-b-4 border-red-800 hover:translate-y-[-2px] active:translate-y-[1px] active:border-b-2 transition-all shadow-lg">
                {t.hero.cta}
            </button>
        </section>
    );
}
